
import { DefineComponent, defineComponent } from "vue";
import { debounce } from "../main";

function initWaterfall(parent: HTMLElement) {
  // 计算waterfall的宽度
  const waterfallWidth = parent.offsetWidth;
  // console.log(waterfallWidth);
  const items = parent.children as any;
  // console.log(items);
  // let items = parent.ch

  // 计算每个item的宽度
  const itemWidth = items[0].offsetWidth;
  // 计算出可以容纳多少列
  let cols = Math.round(waterfallWidth / itemWidth);

  // 使用数组来记录宽度
  let heightOfEachColumn = new Array<number>(cols).fill(0);

  for (let i = 0; i < items.length; i++) {
    let minHeight = Math.min.apply(null, heightOfEachColumn);
    const colIndex = heightOfEachColumn.indexOf(minHeight);
    items[i].style.top = minHeight + "px";
    items[i].style.left = itemWidth * colIndex + "px";
    // 放置好元素之后，更新高度数组
    heightOfEachColumn[colIndex] += items[i].offsetHeight;
  }

  const bottomDom = document.querySelector("#bottom-margin") as HTMLElement;
  bottomDom.style.top=Math.max.apply(null,heightOfEachColumn)+"px";

  //设置margin

  return heightOfEachColumn;
}

