
import Home from "./views/Home.vue";
import Aboutme from "./views/Aboutme.vue";
import BottomBar from "./components/BottomBar.vue";
import BottomButton from "./components/BottomButton.vue";
import TopBar from "./components/TopBar.vue";
import { defineComponent } from "vue";
import Portfolio from "./views/Portfolio.vue";

export default defineComponent({
  components: {
    TopBar,
    BottomBar,
    BottomButton,
    Home,
    Aboutme,
    Portfolio,
  },
  data() {
    return {
      barStatus: "d-none",
      buttonStatus: "",
      navItemsHeight: [0, 0, 0],
      navItems: ["nav-item-1", "nav-item-2", "nav-item-3"],
      appScrollEvent: null,
    };
  },

  methods: {
    showBar() {
      console.log("绑定事件");
      this.buttonStatus = "d-none";
      this.barStatus = "";
    },
    hideBar(e: Event) {
      if (this.barStatus == "") {
        // 当bar存在的时候
        const target = e.target as HTMLElement;
        const targetParent = target.parentElement as HTMLElement;
        if (
          targetParent?.className?.indexOf("bottom-bar-item") < 0 &&
          target?.className?.indexOf("bottom-bar") < 0
        ) {
          this.barStatus = "d-none";
          this.buttonStatus = "";
        }
      }
    },
    scrollEvent() {
    
      if (this.appScrollEvent !== null) {
        clearTimeout(this.appScrollEvent as any);
      }
      (this.appScrollEvent as any) = setTimeout(() => {
          // console.log(this.navItemsHeight);
        this.navItemsHeight = [
          (document.getElementById("home")?.offsetTop as number) - 70 < 0
            ? 0
            : (document.getElementById("home")?.offsetTop as number) - 70,
          (document.getElementById("aboutme")?.offsetTop as number) - 70 < 0
            ? 0
            : (document.getElementById("aboutme")?.offsetTop as number) - 70,
          (document.getElementById("portfolio")?.offsetTop as number) - 70 < 0
            ? 0
            : (document.getElementById("portfolio")?.offsetTop as number) - 70,
        ];
        const currentHeight = document.documentElement.scrollTop as number;
        let index = 0;

        //循环判断当前位置
        for (let i = 0; i < this.navItemsHeight.length; i++) {
          if (currentHeight < this.navItemsHeight[i] - 100) {
            index = i - 1 < 0 ? 0 : i - 1;
            break;
          } else if (!isNaN(this.navItemsHeight[i])) {
            index = i;
          }
        }
        const targetId = this.navItems[index];

        (this.$refs.topBar as any).selectedId = targetId;
        (this.$refs.bottomBar as any).selectedId = targetId;
      }, 300);
      // 本身就是一个防抖函数
      // console.log(this.$refs.topBar.selectedId=)
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.$data.navItemsHeight = [
        (document.getElementById("home")?.offsetTop as number) - 70 < 0
          ? 0
          : (document.getElementById("home")?.offsetTop as number) - 70,
        (document.getElementById("aboutme")?.offsetTop as number) - 70 < 0
          ? 0
          : (document.getElementById("aboutme")?.offsetTop as number) - 70,
        (document.getElementById("portfolio")?.offsetTop as number) - 70 < 0
          ? 0
          : (document.getElementById("portfolio")?.offsetTop as number) - 70,
      ];
    });

    // 监听滚动条事件
    document.addEventListener("scroll", this.scrollEvent);
    // 监听点击事件
    document.addEventListener("click", this.hideBar);
  },
  unmounted() {
    document.removeEventListener("scroll", this.scrollEvent);
    document.removeEventListener("click", this.hideBar);
  },
});
