
import ContactCard from "../components/ContactCard.vue";
import { defineComponent } from "vue";

export default defineComponent({
  name: "Home",
  data() {
    return {
      modal_phone_show: false,
      modal_email_show: false,
      modal_wechat_show: false,
      modal_github_show: false,
      qrcode_url: require("@/assets/qrcode.jpg"),
      screenHeight:""
    };
  },
  components: {
    ContactCard,
  },
  methods: {
    hideModel() {
      console.log("Iknow");
      this.modal_phone_show = false;
      this.modal_email_show = false;
      this.modal_wechat_show = false;
      this.modal_github_show = false;
    }
   
  },
    computed:{
       getScreenHeight():string{
      const contentHeight = document.querySelector("#home")?.clientHeight as number;
      const viewHeight = window.screen.height as number;
      // console.log("content",contentHeight)
      // console.log("view",viewHeight)
      if(contentHeight>viewHeight){
          return"--window-height:"+contentHeight+"px";
          
      }
      return "--window-height:"+(window.screen.width >= 720? "100vh - 60px": window.screen.height + "px - 70px ");
    }
  },
  mounted(){
    this.$nextTick(()=>{
 
      this.screenHeight=this.getScreenHeight;
    })
 

  }
});
