<template>
  <section
    class="container"
    id="portfolio"
    :style="getScreenHeight"

   
  >
    <div class="mb-4 text-center section-title">
      &lt;{{ $t("portfolio") }}&frasl;&gt;
    </div>
    <ul
      class="
        d-flex
        justify-content-md-center justify-content-around
        mb-3
        text-center
        align-items-center
      "
      style="flex-wrap: wrap"
    >
      <div
        class="tab-item col-md-3 px-2"
        :class="[currentTab == '#pills-1' ? 'active' : '']"
        tab-target="#pills-1"
        @click="showWaterFall"
      >
        {{ $t("artillustration") }}
      </div>

      <div
        class="tab-item col-md-3 px-2"
        :class="[currentTab == '#pills-2' ? 'active' : '']"
        tab-target="#pills-2"
        @click="showWaterFall"
      >
        {{ $t("productrender") }}
      </div>

      <div
        class="tab-item col-md-3 px-2"
        :class="[, currentTab == '#pills-3' ? 'active' : '']"
        tab-target="#pills-3"
        @click="showWaterFall"
      >
        {{ $t("uidesign") }}
      </div>
    </ul>

    <div class="tab-content mb-4"  >
      <div v-show="currentTab == '#pills-1'" id="pills-1" >
        <div class="waterfall">
          <div class="item" v-for="(item, index) in portfolioArt" :key="index">
            <img class="img" :src="item" alt="" @load="renderedArtImg++" />
          </div>
        </div>
      </div>

      <div v-show="currentTab == '#pills-2'" id="pills-2">
        <div class="waterfall">
          <div
            class="item"
            v-for="(item, index) in portfolioProduct"
            :key="index"
          >
            <img class="img" :src="item" alt="" />
          </div>
        </div>
      </div>

      <div v-show="currentTab == '#pills-3'" id="pills-3">
        <div class="waterfall">
         <div
            class="item"
            v-for="(item, index) in portfolioUI"
            :key="index"
          >
            <img class="img" :src="item" alt="" />
          </div>
        </div>
      </div>
    </div>
    <section id="bottom-margin" style="width:100%; left:0; padding:0; height:100px; background:white; position:relative"></section>
  </section>
</template>

<script lang="ts">
import { DefineComponent, defineComponent } from "vue";
import { debounce } from "../main";

function initWaterfall(parent: HTMLElement) {
  // 计算waterfall的宽度
  const waterfallWidth = parent.offsetWidth;
  // console.log(waterfallWidth);
  const items = parent.children as any;
  // console.log(items);
  // let items = parent.ch

  // 计算每个item的宽度
  const itemWidth = items[0].offsetWidth;
  // 计算出可以容纳多少列
  let cols = Math.round(waterfallWidth / itemWidth);

  // 使用数组来记录宽度
  let heightOfEachColumn = new Array<number>(cols).fill(0);

  for (let i = 0; i < items.length; i++) {
    let minHeight = Math.min.apply(null, heightOfEachColumn);
    const colIndex = heightOfEachColumn.indexOf(minHeight);
    items[i].style.top = minHeight + "px";
    items[i].style.left = itemWidth * colIndex + "px";
    // 放置好元素之后，更新高度数组
    heightOfEachColumn[colIndex] += items[i].offsetHeight;
  }

  const bottomDom = document.querySelector("#bottom-margin") as HTMLElement;
  bottomDom.style.top=Math.max.apply(null,heightOfEachColumn)+"px";

  //设置margin

  return heightOfEachColumn;
}

export default defineComponent({
  name: "Portfolio",
  data() {
    return {
      currentTab: "#pills-1",
      currentWidth: document.documentElement.clientWidth,
      portfolioArt: [
        require("../assets/portfolio/art/art1.jpg"),
        require("../assets/portfolio/art/art2.jpg"),
        require("../assets/portfolio/art/art3.jpg"),
        require("../assets/portfolio/art/art4.jpg"),
        require("../assets/portfolio/art/art5.jpg"),
        require("../assets/portfolio/art/art6.jpg"),
        require("../assets/portfolio/art/art7.jpg"),
        require("../assets/portfolio/art/art8.jpg"),
        require("../assets/portfolio/art/art9.jpg"),
        require("../assets/portfolio/art/art10.jpg"),
        require("../assets/portfolio/art/art11.jpg"),
        require("../assets/portfolio/art/art12.jpg"),
        require("../assets/portfolio/art/art13.jpg"),
      ],
      portfolioProduct: [
        require("../assets/portfolio/product/p1.jpg"),
        require("../assets/portfolio/product/p2.jpg"),
        require("../assets/portfolio/product/p3.jpg"),
        require("../assets/portfolio/product/p4.jpg"),
        require("../assets/portfolio/product/p5.jpg"),
        require("../assets/portfolio/product/p6.jpg"),
        require("../assets/portfolio/product/p28.jpg"),
        require("../assets/portfolio/product/p29.jpg"),
        require("../assets/portfolio/product/p30.jpg"),
        require("../assets/portfolio/product/p31.jpg"),
        require("../assets/portfolio/product/p32.jpg"),
        require("../assets/portfolio/product/p33.jpg"),
        require("../assets/portfolio/product/p34.jpg"),
        require("../assets/portfolio/product/p35.jpg"),
        require("../assets/portfolio/product/p7.jpg"),
        require("../assets/portfolio/product/p8.jpg"),
        require("../assets/portfolio/product/p9.jpg"),
        require("../assets/portfolio/product/p10.jpg"),
        require("../assets/portfolio/product/p11.jpg"),
        require("../assets/portfolio/product/p12.jpg"),
        require("../assets/portfolio/product/p13.jpg"),
        require("../assets/portfolio/product/p15.jpg"),
        require("../assets/portfolio/product/p17.jpg"),
        require("../assets/portfolio/product/p18.jpg"),
        require("../assets/portfolio/product/p19.jpg"),
        require("../assets/portfolio/product/p20.jpg"),
        require("../assets/portfolio/product/p21.jpg"),
        require("../assets/portfolio/product/p22.jpg"),
        require("../assets/portfolio/product/p23.jpg"),
        require("../assets/portfolio/product/p24.jpg"),
        require("../assets/portfolio/product/p25.jpg"),
        require("../assets/portfolio/product/p26.jpg"),
        require("../assets/portfolio/product/p27.jpg"),
      ],
      portfolioUI: [
        require("../assets/portfolio/ui/u1.png"),
        require("../assets/portfolio/ui/u2.png"),
        require("../assets/portfolio/ui/u3.png"),
        require("../assets/portfolio/ui/u4.png"),
        require("../assets/portfolio/ui/u5.png"),
        require("../assets/portfolio/ui/u6.png"),
        require("../assets/portfolio/ui/u8.png"),
        require("../assets/portfolio/ui/u7.png"),
        require("../assets/portfolio/ui/u9.png"),

        ],
      totalArtImg: 13,
      renderedArtImg: 0,
    };
  },
  methods: {
    showWaterFall(e: Event) {
      const target = e.currentTarget as HTMLElement;
      const id = target.getAttribute("tab-target") as string;
      this.currentTab = id;
    },
    updateWaterFall() {
      this.$nextTick(() => {
        // 找到show 的waterfall
        const parent = document.querySelector(
          this.currentTab + " .waterfall"
        ) as HTMLElement;

        // 判断是否有item元素
        if (
          parent?.firstElementChild != null &&
          parent.firstElementChild?.className.indexOf("item") != -1
        ) {
          initWaterfall(parent);
        }
      });
    },
  },
  computed: {
    getScreenHeight() {
      return {
        "--window-height":
          window.screen.width >= 720
            ? "100vh - 60px"
            : window.screen.height + "px - 3rem",
      };
    },
  },
  watch: {
    renderedArtImg(newVal, oldVal) {
      if (newVal == this.totalArtImg) {
        this.updateWaterFall();
      }
    },
  },
  mounted() {
    window.addEventListener("resize", this.updateWaterFall);
  },
  updated() {
    // 找到show 的waterfall
    const parent = document.querySelector(
      this.currentTab + " .waterfall"
    ) as HTMLElement;
    // 判断是否有item元素
    if (
      parent?.firstElementChild != null &&
      parent.firstElementChild?.className.indexOf("item") != -1
    ) {
      initWaterfall(parent);
    }
  },
  unmounted() {
    window.removeEventListener("resize", this.updateWaterFall);
  },
});
</script>

